import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { SeekersService } from "./seekers.service";
import { Validators, FormGroup, FormBuilder } from "@angular/forms";
import { CustomvalidationService } from "../../services/customvalidation.service";
import { ImageCroppedEvent, base64ToFile } from "ngx-image-cropper";

@Component({
	selector: "app-seekers",
	templateUrl: "./seekers.component.html",
	styleUrls: ["./seekers.component.css"],
})
export class SeekersComponent implements OnInit {
	seeker_stp_1: boolean;
	seeker_stp_2: boolean;
	seeker_stp_3: boolean;
	seeker_stp_4: boolean;

	seekerForm: any = {
		Step1: FormGroup,
		Step2: FormGroup,
		Step3: FormGroup,
		Step4: FormGroup,
	};

	isSubmitted: any = {
		Step1: false,
		Step2: false,
		Step3: false,
		Step4: false,
		Step5: false,
	};

	verifyingOTP: boolean = false;
	loading: boolean = false;
	created_user: any = {};
	seeker_data: any;
	user_name: string = "";
	file: any;
	avatar_preview: any = "./assets/img/default-avatar.jpg";

	imageChangedEvent: any = "";
	croppedImage: any = "";
	fileLoaded: boolean = false;
	resendCodeLoading: boolean = false;
	smartCities: any;

	constructor(private _ss: SeekersService, private toastr: ToastrService, private router: Router, private fb: FormBuilder, private customValidator: CustomvalidationService) {}

	ngOnInit() {
		this.listSmartCities();
		this.seeker_stp_1 = false;
		this.seeker_stp_2 = true;
		this.seeker_stp_3 = true;
		this.seeker_stp_4 = true;
		this.seekerForm = {
			Step1: this.fb.group({
				fullname: ["", [Validators.required, Validators.pattern("[a-zA-Z .]*")]],
				email: ["", [Validators.required, Validators.email,Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')], this.customValidator.emailAvailabilityValidator.bind(this.customValidator)],
				phnum: ["", [Validators.required, Validators.pattern("(0|91)?[5-9][0-9]{9}")], this.customValidator.phoneAvailabilityValidator.bind(this.customValidator)],
				landlineCode: ["00", Validators.pattern("^\\d{2,8}$")],
				landlineNumber: ["", Validators.pattern("^\\d{8}$")],
			}),
			Step2: this.fb.group({
				city: ["", [Validators.required, Validators.min(1)]],
				department: ["",Validators.pattern("^[a-zA-Z]+$")],
				designation: ["",Validators.pattern("^[a-zA-Z]+$")],
			}),
			Step3: this.fb.group(
				{
					password: ["", [Validators.required, Validators.pattern("^(?=.*[0-9])(?=.*[!@#$%^&*])(?=.*[A-Z])[a-zA-Z0-9!@#$%^&*]{8,}$")]],
					confirmPassword: ["", [Validators.required]],
				},
				{ validator: this.customValidator.MatchPassword("password", "confirmPassword") }
			),
			Step4: this.fb.group({
				otp: ["", [Validators.required, Validators.minLength(6)]],
			}),
		};
	}

	async listSmartCities() {
		try {
			this.smartCities = await this._ss.getSmartCities();
			// console.log(this.smartCities);
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	onSubmit(formIndex: string) {
		switch (formIndex) {
			case "Step1": {
				this.isSubmitted.Step1 = true;
				break;
			}
			case "Step2": {
				this.isSubmitted.Step2 = true;
				break;
			}
			case "Step3": {
				this.isSubmitted.Step3 = true;
				break;
			}
			case "Step4": {
				this.isSubmitted.Step4 = true;
				break;
			}
		}
	}

	get formControlStep1() {
		return this.seekerForm.Step1.controls;
	}

	get formControlStep2() {
		return this.seekerForm.Step2.controls;
	}

	get formControlStep3() {
		return this.seekerForm.Step3.controls;
	}

	get formControlStep4() {
		return this.seekerForm.Step4.controls;
	}

	get formControlStep5() {
		return this.seekerForm.Step5.controls;
	}

	ShowNextStep(step: any) {
		if (step == "step2" && this.seekerForm.Step1.valid) {
			this.user_name = this.seekerForm.Step1.value.fullname;
			this.seeker_stp_1 = true;
			this.seeker_stp_2 = false;
		} else if (step == "step3" && this.seekerForm.Step2.valid) {
			this.seeker_stp_2 = true;
			this.seeker_stp_3 = false;
		} else if (step == "step4" && this.seekerForm.Step3.valid) {
			this.seeker_stp_3 = true;
			this.seeker_stp_4 = false;
		} else {
			this.toastr.error("", "Please provide the required values to proceed further");
		}
	}

	ShowPreviousStep(step: any) {
		if (step == "step2") {
			this.seeker_stp_1 = false;
			this.seeker_stp_2 = true;
		} else if (step == "step3") {
			this.seeker_stp_2 = false;
			this.seeker_stp_3 = true;
		}
	}

	setChosenFile(event) {
		if (event.target.files && event.target.files[0]) {
			this.file = (event.target as HTMLInputElement).files[0];
			const reader = new FileReader();
			reader.readAsDataURL(this.file);
			// console.log("File", this.file);
			reader.onload = (e) => {
				this.avatar_preview = reader.result;
			};
			document.getElementById("imageModal").click();
		}
	}

	fileChangeEvent(event: any): void {
		this.imageChangedEvent = event;
	}
	imageCropped(event: ImageCroppedEvent) {
		this.croppedImage = event.base64;
	}
	imageLoaded() {
		this.fileLoaded = true;
	}
	cropperReady() {
		// cropper ready
	}
	loadImageFailed() {
		// show message
	}

	saveCropedImage() {
		const fileBeforeCrop = this.imageChangedEvent.target.files[0];
		this.file = new File([base64ToFile(this.croppedImage)], fileBeforeCrop.name, { type: fileBeforeCrop.type });
		this.avatar_preview = this.croppedImage;
		document.getElementById("imageModal").click();
	}

	gotoStep(step: any) {
		switch (step) {
			case "step1": {
				this.seeker_stp_1 = false;
				this.seeker_stp_2 = true;
				this.seeker_stp_3 = true;
				this.seeker_stp_4 = true;
				break;
			}
			case "step2": {
				this.seeker_stp_1 = true;
				this.seeker_stp_2 = false;
				this.seeker_stp_3 = true;
				this.seeker_stp_4 = true;
				break;
			}
			case "step3": {
				this.seeker_stp_1 = true;
				this.seeker_stp_2 = true;
				this.seeker_stp_3 = false;
				this.seeker_stp_4 = true;
				break;
			}
			case "step4": {
				this.seeker_stp_1 = true;
				this.seeker_stp_2 = true;
				this.seeker_stp_3 = true;
				this.seeker_stp_4 = false;
				break;
			}
		}
	}

	saveUser() {
		if (this.seekerForm.Step1.valid && this.seekerForm.Step2.valid && this.seekerForm.Step3.valid) {
			this.loading = true;
			var post_data = {
				data: {
					email: this.seekerForm.Step1.value.email,
					mobile_code: "+91",
					mobile: this.seekerForm.Step1.value.phnum,
					password: this.seekerForm.Step3.value.password,
					role_seeker: true,
					social_signup_via: "",
				},
			};
			var seeker_post_data = {
				data: {
					fullname: this.seekerForm.Step1.value.fullname,
					frg_smart_city_id: this.seekerForm.Step2.value.city,
					department: this.seekerForm.Step2.value.department,
					designation: this.seekerForm.Step2.value.designation,
					phone_code: "+91",
					phone: this.seekerForm.Step1.value.phnum,
					landline_num:
						this.seekerForm.Step1.value.landlineCode == "" || this.seekerForm.Step1.value.landlineNumber == ""
							? ""
							: `${this.seekerForm.Step1.value.landlineCode}-${this.seekerForm.Step1.value.landlineNumber}`,
					frg_user_id: null,
				},
			};

			this._ss.saveUserApi(post_data).subscribe(
				(response: any) => {
					if (response.status == true) {
						this.created_user = response.data;
						seeker_post_data.data.frg_user_id = response.data.id;
						this._ss.saveSeekerApi(seeker_post_data).subscribe((seekerResponse: any) => {
							this.seeker_data = seekerResponse.data;
							this.saveAvatar();
							this.loading = false;
							this.ShowNextStep("step4");
						});
					}
				},
				(response: any) => {
					this.loading = false;
					// console.log(response);
					if (response.error.data.code === "E_UNIQUE" && response.error.status === false) {
						this.toastr.error("", response.error.msg);
					} else {
						this.toastr.error("", "Something went wrong please try again");
					}
				}
			);
		} else {
			this.toastr.error("", "Please provide the required values to proceed further");
		}
	}

	saveAvatar() {
		if (this.file) {
			let formData = new FormData();
			formData.append("slug", this.seeker_data.id);
			formData.append("avatar", this.file, this.file.name);
			// console.log("FornData", formData);
			this._ss.uploadAvataar(formData).subscribe((response: any) => {
				// console.log("Save Avatar", response);
			});
		}
	}

	async resendCode() {
		try {
			this.resendCodeLoading = true;
			let resp: any = await this._ss.resendVerficationCode({
				slug: {
					id: this.created_user.id,
				},
			});
			this.resendCodeLoading = false;
			if (resp.status == true) {
				this.toastr.info("", resp.msg);
			} else {
				this.toastr.error("", resp.msg);
			}
		} catch (err) {
			this.resendCodeLoading = false;
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	verifyOtp() {
		if (this.seekerForm.Step1.valid && this.seekerForm.Step2.valid && this.seekerForm.Step3.valid && this.seekerForm.Step4.valid) {
			let post_data = {
				slug: {
					id: this.created_user.id,
				},
				data: {
					email_otp: this.seekerForm.Step4.value.otp,
				},
			};
			this.verifyingOTP = true;
			this._ss.verifyOtp(post_data).subscribe((response: any) => {
				this.verifyingOTP = false;
				if (response.status == true) {
					// this.toastr.success("", "User verified successfully");
					this.router.navigate(["/login"]).then(() => {
						this.toastr.warning("Your account is being reviewed by the Admin and is pending approval. You will receive an email once it is approved.", "Pending for Approval");
					});
				} else {
					this.toastr.error("", "Verification code entered is invalid");
				}
			});
		} else {
			this.verifyingOTP = false;
			this.toastr.error("", "Please provide the required values to proceed further");
		}
	}
}
